<template>
    <yd-layout>
        <div class="h_detail">
            <yd-flexbox>
                <yd-flexbox-item>{{detail.KpiName}} — {{detail.UserName}}</yd-flexbox-item>
                <div class="person_zb">个人指标</div>
            </yd-flexbox>
            <yd-flexbox>
                <yd-flexbox-item class="state">状态：{{detail.Status}}</yd-flexbox-item>
            </yd-flexbox>
        </div>
        <div class="addTask">添加任务<img class="add_img" src="../../../static/images/performance/addTask@3x.png" alt="" @click="addFlag=true;addOrEdit=0;PlanedWork='';ActualWork=''" /></div>
        <div class="addText" v-if="addFlag">
            <trsTextarea class="trs1" placeholder="请填写工作任务" v-model="PlanedWork"></trsTextarea>
            <trsTextarea class="trs1 trs2" placeholder="请填写实际完成情况" v-model="ActualWork"></trsTextarea>
            <div class="yes" @click="addTask()"></div>
            <div class="no" @click="addFlag=false"></div>
        </div>
        <div class="planList" v-for="(item, index) in ItemList" :key="index">
            <div class="planOne" v-if="!editFlag||editNum!=index">
                <div class="planTitle">{{index+1}}.工作任务：</div>
                <div class="planInfo" v-html="item.PlanedWork"></div>
                <div class="planEdit" @click="editFlag=true;editNum=index;editPlanedWork=item.PlanedWork;editActualWork=item.ActualWork;editItemId=item.ItemId"></div>
                <div class="planDel" @click="deleteTask(item.ItemId)"></div>
            </div>
            <div class="oneInfo" v-if="!editFlag||editNum!=index">
                <div class="infoWork finshed">
                    <div class="iw_title">完成情况：</div>
                    <div class="infoDetail" v-html="item.ActualWork"></div>
                </div>
            </div>
            <div class="addText" v-if="editFlag&&editNum==index">
                <trsTextarea class="trs1" placeholder="请填写工作任务" v-model="editPlanedWork"></trsTextarea>
                <trsTextarea class="trs1 trs2" placeholder="请填写实际完成情况" v-model="editActualWork"></trsTextarea>
                <div class="yes" @click="editTask()"></div>
                <div class="no" @click="editFlag=false"></div>
            </div>
        </div>
        <div class="score">
            <yd-flexbox>
                <yd-flexbox-item class="yd_score">量表整体自评分：</yd-flexbox-item>
                <trsTextarea v-model="score"></trsTextarea>
                <div class="remind">请填写整数</div>
            </yd-flexbox>
        </div>
        <div class="btn_bot">
            <div class="per_leftBtn" :class="{per_rightBtn:ItemList.length==0}" @click="ItemList.length==0?$dialog.toast({mes: '请添加任务',timeout: 2000}):$router.push('/performanceList')">关闭</div>
            <div class="per_rightBtn" :class="{submitBtn:score!=''&&ItemList.length>0}" @click="submitData()">提交</div>
        </div>
        <yd-tabbar class="bot_perf" slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item link="" title="代办事项" active>
                <yd-icon name="grdb" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item title="我的考评" link="/myPerformance">
                <yd-icon name="wdkp" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            planInfo: '',
            addFlag: false,
            detail: {},
            ItemList: [],
            ActualWork: '',
            PlanedWork: '',
            ItemId: 0,
            score: '',
            editFlag: false,
            editNum: -1,
            editActualWork: '',
            editPlanedWork: '',
            editItemId: 0
        }
    },
    mounted() {
        document.setTitle('绩效管理');
        let vm = this;
        vm.detailData();
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        detailData() {
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/MonthlyKpi/GetSingleDoMonthlyKpi', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                Id: vm.$route.params.id
            }, function(res) {
                console.log(JSON.parse(res.JsonData));
                vm.detail = JSON.parse(res.JsonData);
                vm.ItemList = vm.detail.ItemList;
            }, function(res) {
                if (res.response.data.IsSuccess == false) {
                    vm.$dialog.toast({
                        mes: '该流程已处理',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/performanceList');
                        }
                    });
                }
            });
        },
        addTask() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/MonthlyKpi/AddMonthlyKpiItem', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                KpiId: vm.$route.params.id,
                PlanedWork: vm.transferString(vm.PlanedWork),
                ActualWork: vm.transferString(vm.ActualWork)
            }, function(res) {
                vm.$dialog.toast({
                    mes: '添加成功',
                    timeout: 2000,
                    callback: () => {
                        vm.addFlag = false;
                        vm.detailData();
                    }
                });
            });
        },
        editTask() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/MonthlyKpi/EditMonthlyKpiItem', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                ItemId: vm.editItemId,
                PlanedWork: vm.editPlanedWork,
                ActualWork: vm.editActualWork
            }, function(res) {
                vm.$dialog.toast({
                    mes: '修改成功',
                    timeout: 2000,
                    callback: () => {
                        vm.editFlag = false;
                        vm.detailData();
                    }
                });
            });
        },
        deleteTask(ItemId) {
            console.log(ItemId);
            let vm = this;
            vm.get(vm.conf.api + '/HRMSApi/api/MonthlyKpi/DeleteMonthlyKpiItem', {
                token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                ItemId: ItemId
            }, function(res) {
                vm.$dialog.toast({
                    mes: '删除成功',
                    timeout: 2000,
                    callback: () => {
                        vm.detailData();
                    }
                });
            });
        },
        submitData() {
            let vm = this;
            if (vm.ItemList.length == 0) {
                vm.$dialog.toast({
                    mes: '请添加任务',
                    timeout: 2000
                });
            } else if (vm.score == '') {
                vm.$dialog.toast({
                    mes: '请填写自评分',
                    timeout: 2000
                });
            } else if (Number.isInteger(Number(vm.score)) == false || Number(vm.score) < 0 || Number(vm.score) > 100) {
                vm.$dialog.toast({
                    mes: '请填写0~100之间的整数',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/HRMSApi/api/MonthlyKpi/SubmitMonthlyKpi', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    KpiId: vm.$route.params.id,
                    SelfScore: vm.score
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '提交成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/performanceList/');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import './performance.less';
.yd-icon-grdb:before,
.yd-icon-wdkp:before {
    display: block;
    content: "";
}

.yd-icon-grdb:before {
    width: 0.41rem;
    height: 0.41rem;
    background: url(../../../static/images/performance/grdb@2x.png) no-repeat 0 0;
    background-size: 0.4rem;
}

.yd-icon-wdkp:before {
    width: 0.35rem;
    height: 0.35rem;
    background: url(../../../static/images/performance/wdkp.png) no-repeat 0 0;
    background-size: 0.35rem;
}
</style>


